class AudioPlayer {
    constructor(props) {
        this.props = props;

        this.audio = new Audio(this.props.song);
        this.audio.crossOrigin = "anonymous";
        this.audio.controls = false;

        this.button = document.createElement("button");
        this.button.innerHTML = "Play";
        this.button.style=`
            position: fixed;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        `
        
        this.addEventListeners();
    }

    getByteFrequencyData() {
        this.analyser.getByteFrequencyData(this.frequencyData);
        return this.frequencyData;
    }

    addEventListeners = () => {
        this.button.addEventListener("click", this.handleClick);
        if(this.props.audioFinishedPlaying) this.audio.addEventListener('ended', this.props.audioFinishedPlaying);
    }

    removeEventListeners = () => {
        this.button.removeEventListener("click", this.handleClick);
        if(this.props.audioFinishedPlaying) this.audio.removeEventListener('ended', this.props.audioFinishedPlaying);
    }

    handleClick = () => {
        this.audioContext = new (window.AudioContext || window.webkitAudioContext)();
        this.analyser = this.audioContext.createAnalyser();
        this.analyser.fftSize = this.props.frequencySize || 2048;
        this.frequencyData = new Uint8Array(this.analyser.frequencyBinCount);
        
        const audioSource = this.audioContext.createMediaElementSource(this.audio)
        audioSource.connect(this.analyser)
        this.analyser.connect(this.audioContext.destination)
        this.audio.play();

        if(this.props.onPlay) this.props.onPlay();
    }
}

export default AudioPlayer;